html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.Flights {
  width: 100%; }

.Flight_Box {
  width: 100%;
  background-color: #ffffff;
  border: solid 1px #e7e8f7;
  border-radius: 10px;
  display: flex;
  padding: 15px 20px 15px 20px;
  margin-bottom: 5px;
  cursor: pointer; }
  .Flight_Box:hover {
    background-color: #f8f8f8; }

.Flights_Details {
  width: 55%;
  display: flex;
  font-size: 14px;
  overflow: hidden; }

.Flight_Number {
  width: 45%;
  color: #e61f1d;
  font-size: 14px; }

.BaseBody {
  font-size: 12px;
  color: #666666;
  padding-top: 8px; }

.header {
  font-size: 14px;
  color: #666666; }

.Base_Details {
  width: 65%;
  color: #000000; }

._Date_Time {
  display: flex;
  width: 45%; }

._Date1 {
  width: 50%;
  justify-content: flex-end; }

.displayflex {
  display: flex;
  padding-top: 8px; }

._Time1 {
  margin-left: 10px; }

.margin-left {
  margin-left: 20px; }
