html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.TemplateViewHeader {
  margin: 10px 0;
  margin-top: 21px;
  width: 65%;
  height: 26px;
  overflow: hidden; }
  .TemplateViewHeader .HeaderText {
    padding-left: 20px;
    color: #666666;
    font-weight: bold;
    opacity: 0.7; }

.TemplateTaskList {
  width: 65%;
  height: 100%;
  background-color: #ffffff;
  border: solid 1px #e7e8f7;
  border-radius: 10px;
  padding: 15px 29px 0px 20px; }
  .TemplateTaskList .TotalTask_addTask {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: stretch;
    margin: 20px 0px 7px 0px; }
    .TemplateTaskList .TotalTask_addTask .TotalTask {
      flex: 1;
      color: #666666; }
    .TemplateTaskList .TotalTask_addTask .addTask {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      flex-direction: row;
      align-items: stretch; }
  .TemplateTaskList .searchcontainer {
    width: 100%;
    border-top: solid 1px #e7e8f7;
    background-color: #f8f8f8;
    padding: 7.5px 11px 8px 9px; }
    .TemplateTaskList .searchcontainer .searchbar {
      width: 100%;
      border: solid 1px #f0f0f0;
      background-color: #ffffff; }
  .TemplateTaskList .TaskHeading {
    display: flex;
    font-size: 14px;
    font-weight: bold; }
    .TemplateTaskList .TaskHeading .Task_wing {
      width: 20%; }
    .TemplateTaskList .TaskHeading .Taskname_Description {
      width: 35%; }
    .TemplateTaskList .TaskHeading .Departmentname {
      width: 15%; }
    .TemplateTaskList .TaskHeading .Taskstart {
      width: 15%; }
    .TemplateTaskList .TaskHeading .Taskend {
      width: 11%; }
  .TemplateTaskList .assignedTaskcontainer {
    padding: 13px 0px 10px 0px;
    width: 100%;
    font-size: 12px;
    cursor: pointer;
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: stretch; }
    .TemplateTaskList .assignedTaskcontainer:hover {
      background-color: #f8f8f8; }
      .TemplateTaskList .assignedTaskcontainer:hover .Taskdelete {
        display: block; }
    .TemplateTaskList .assignedTaskcontainer .Task_wing {
      width: 20%; }
      .TemplateTaskList .assignedTaskcontainer .Task_wing .Taskno {
        color: #000000; }
      .TemplateTaskList .assignedTaskcontainer .Task_wing .wing {
        color: #666666;
        padding-top: 3px; }
    .TemplateTaskList .assignedTaskcontainer .Taskname_Description {
      width: 35%; }
      .TemplateTaskList .assignedTaskcontainer .Taskname_Description .Taskname {
        color: #000000; }
      .TemplateTaskList .assignedTaskcontainer .Taskname_Description .TaskDescription {
        color: #666666;
        padding-top: 3px; }
    .TemplateTaskList .assignedTaskcontainer .Departmentname {
      width: 15%;
      color: #666666; }
    .TemplateTaskList .assignedTaskcontainer .Taskstart {
      width: 15%;
      color: #999999; }
    .TemplateTaskList .assignedTaskcontainer .Taskend {
      width: 11%;
      color: #999999; }
    .TemplateTaskList .assignedTaskcontainer .Taskdelete {
      display: none; }
  .TemplateTaskList .line {
    border: solid 1px #ededed;
    width: 100%; }
