html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.TaskHeading {
  width: 100%;
  margin: 19px 0px 11px 0px;
  display: flex;
  font-size: 12px;
  color: #666666; }

.TaskList {
  margin-top: 5px;
  width: 55%;
  color: #666666; }

.task-search-header {
  position: relative; }
  .task-search-header .task-search-icon {
    position: absolute;
    width: 18px;
    height: 22px;
    line-height: 22px;
    color: #999999;
    left: 7px; }
  .task-search-header .searchbartask {
    width: 100%;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    padding: 6px 0px 6px 29px;
    margin-left: auto;
    font-size: 12px;
    height: 22px; }

.task1 {
  margin-bottom: 19px; }
