html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.task {
  width: 50%;
  height: 100%;
  border-right: solid 1px #e7e8f7; }
  .task .searchcover {
    background-color: #f8f8f8;
    border-top: solid 1px #e7e8f7;
    padding: 7.5px 16px 8px 9px; }
    .task .searchcover .searchbox {
      width: 100%; }
  .task .eng_ground_sec {
    display: flex;
    align-content: flex-end;
    width: 100%;
    cursor: pointer;
    color: #e61f1d;
    overflow-x: auto; }
    .task .eng_ground_sec > div:hover {
      color: #000000; }
  .task .bottomline {
    border-top: solid 1px #e7e8f7; }
  .task .isactive {
    background-color: rgba(241, 27, 54, 0.82);
    border-radius: 4px; }
    .task .isactive .task_no_wing {
      width: 25%; }
      .task .isactive .task_no_wing .taskno {
        color: #fff; }
      .task .isactive .task_no_wing .wing {
        color: #fff; }
    .task .isactive .task_name_details .task_name {
      color: #fff; }
    .task .isactive .task_name_details .task_details {
      color: #fff; }

.tasklist_addtask {
  padding: 10px 0px 10px 4px;
  font-size: 12px;
  display: flex;
  cursor: pointer; }
  .tasklist_addtask:hover {
    background-color: #f8f8f8; }
    .tasklist_addtask:hover .tick {
      width: 13px; }
  .tasklist_addtask.isactive:hover {
    background-color: rgba(241, 27, 54, 0.82); }
  .tasklist_addtask .task_no_wing {
    width: 25%; }
    .tasklist_addtask .task_no_wing .taskno {
      color: #000000; }
    .tasklist_addtask .task_no_wing .wing {
      color: #666666;
      padding-top: 3px; }
  .tasklist_addtask .task_name_details {
    width: 64%; }
    .tasklist_addtask .task_name_details .task_name {
      color: #000000; }
    .tasklist_addtask .task_name_details .task_details {
      padding-top: 3px;
      color: #666666; }
  .tasklist_addtask .tick {
    width: 13px;
    height: 13px;
    align-self: center; }
  .tasklist_addtask .bottomline_addtask {
    border-top: solid 1px #e7e8f7; }
