html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.turnequpfleet {
  width: 100%;
  color: #666666;
  display: flex; }
  .turnequpfleet .turntype {
    width: 32%; }
    .turnequpfleet .turntype .turntypedrop {
      padding: 10px 15px 0 0;
      color: #000000; }
  .turnequpfleet .plannedTAT {
    width: 25%; }
    .turnequpfleet .plannedTAT .plannedTATvalue {
      color: #000000;
      padding: 10px 0px 0px 0px; }
  .turnequpfleet .equptype {
    width: 25%; }
    .turnequpfleet .equptype .equptypedrop {
      padding: 10px 16px 0 0;
      color: #000000; }
  .turnequpfleet .fleetype {
    width: 20%; }
    .turnequpfleet .fleetype .fleettypeinput {
      padding: 10px 0px 0 0;
      color: #9b9b9b; }

.effective_expiry {
  width: 100%;
  color: #666666;
  display: flex;
  margin: 10px 0px 5px 0px; }
  .effective_expiry .effective {
    width: 31.5%; }
    .effective_expiry .effective .effective_value {
      padding: 10px 15px 0 0;
      color: #000000; }
  .effective_expiry .expiry {
    width: 25%; }
    .effective_expiry .expiry .expiry_value {
      padding: 10px 15px 0 0;
      color: #000000; }
