html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.dependencytask {
  width: 50%;
  background-color: #f8f8f8;
  padding: 8px 10px 0px 14px; }
  .dependencytask .taskdurationtext {
    color: #666666; }
  .dependencytask .show-order {
    display: flex;
    padding-top: 11px;
    flex-wrap: wrap; }
    .dependencytask .show-order div {
      background: red;
      margin: 2px;
      padding: 6px;
      font-size: 13px;
      color: #fff;
      border-radius: 6px; }
  .dependencytask .startendtime {
    display: flex;
    margin: 10px 0px 12px 0px; }
    .dependencytask .startendtime .starttime {
      width: 82px; }
    .dependencytask .startendtime .endtime {
      width: 82px;
      padding-left: 10px; }
  .dependencytask .dcptext {
    color: #666666;
    margin-bottom: 10px; }
  .dependencytask .togglerparent {
    width: 80px;
    height: 22px; }
  .dependencytask .selectdeptasktext {
    margin: 18px 0px 10px 0px; }
  .dependencytask .dependencysearchbar {
    width: 100%; }
  .dependencytask .tasklist_addtask {
    padding: 10px 0px 10px 4px;
    font-size: 12px;
    display: flex;
    cursor: pointer; }
    .dependencytask .tasklist_addtask:hover {
      background-color: #f8f8f8; }
      .dependencytask .tasklist_addtask:hover .tick {
        cursor: pointer;
        width: 13px; }
    .dependencytask .tasklist_addtask .task_no_wing {
      width: 25%; }
      .dependencytask .tasklist_addtask .task_no_wing .taskno {
        color: #000000; }
      .dependencytask .tasklist_addtask .task_no_wing .wing {
        color: #666666;
        padding-top: 3px; }
    .dependencytask .tasklist_addtask .task_name_details {
      width: 64%; }
      .dependencytask .tasklist_addtask .task_name_details .task_name {
        color: #000000; }
      .dependencytask .tasklist_addtask .task_name_details .task_details {
        padding-top: 3px;
        color: #666666; }
    .dependencytask .tasklist_addtask .tick {
      width: 13px;
      height: 13px;
      align-self: center; }
  .dependencytask .bottomline_addtask {
    border-top: solid 1px #e7e8f7; }
