html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.CreateDepartmentText {
  margin: 15px 0 10px 0px;
  width: 100%;
  font-size: 12px;
  color: #666666; }

.DepartmentDetails {
  background-color: #ffffff;
  border: solid 1px #f0f0f0;
  padding: 17px 9.2px 24px 15px;
  color: #000000;
  border-radius: 10px; }
  .DepartmentDetails .DepartmentText {
    opacity: 0.5; }
  .DepartmentDetails .DepartmentInputelements {
    width: 100%;
    margin: 7px 15.2px 16px 0px;
    border-radius: 5px;
    border: solid 1px #d3d3d3;
    padding: 10px 0px 12px 19px; }
    .DepartmentDetails .DepartmentInputelements:disabled {
      opacity: 0.9;
      cursor: not-allowed; }
