html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.DepartmentlistText {
  color: #666666;
  font-size: 12px;
  margin: 17px 0px 10px 0px;
  cursor: default; }

.Departmentlist {
  width: 100%;
  border: solid 1px #f0f0f0;
  background-color: #ffffff;
  margin-bottom: 18px;
  cursor: default; }
  .Departmentlist .Departmentlistwrap {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: stretch;
    padding: 13px 19.2px 10px 10.5px;
    border-bottom: solid 1px #ededed;
    position: relative; }
    .Departmentlist .Departmentlistwrap:hover {
      background-color: #f8f8f8; }
    .Departmentlist .Departmentlistwrap:hover .delete-icon-div {
      display: flex;
      position: absolute;
      padding: 0px 0px 0px 1%;
      cursor: pointer; }
    .Departmentlist .Departmentlistwrap .Departmentid {
      width: 35%;
      color: #000000;
      font-size: 12px; }
    .Departmentlist .Departmentlistwrap .Departmentname {
      width: 45%;
      padding-left: 15px;
      font-size: 12px; }
    .Departmentlist .Departmentlistwrap .Departmentcode {
      width: 20%;
      color: #666666;
      font-size: 12px;
      display: flex;
      justify-content: flex-end;
      flex-direction: row;
      align-items: stretch; }
    .Departmentlist .Departmentlistwrap .delete-icon-div {
      display: none; }
