html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.Task {
  width: 100%;
  background-color: #ffffff;
  cursor: default; }

.Task-item {
  border-radius: 0px;
  border-bottom: solid 1px #f0f0f0;
  padding: 12px 19.2px 7px 15px;
  display: flex;
  cursor: pointer;
  position: relative; }
  .Task-item:hover {
    background-color: #f8f8f8; }
  .Task-item:hover .delete-icon-div {
    display: flex;
    position: absolute;
    padding: 0.2% 0px 0px 1%;
    cursor: pointer; }
  .Task-item .TaskNumber {
    width: 35%;
    font-size: 12px;
    color: #000000; }
  .Task-item .TaskDescription {
    width: 45%; }
  .Task-item .TaskCategory {
    width: 20%;
    font-size: 12px;
    color: #666666;
    display: flex;
    justify-content: flex-end; }
  .Task-item .delete-icon-div {
    display: none; }

.Taskrow2 {
  font-size: 12px;
  color: #666666;
  padding-top: 3px; }
