html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.TurnManagementHeader {
  margin: 10px 0;
  margin-top: 21px;
  width: 65%;
  height: 22px;
  overflow: hidden; }

.TurnManagementwrap {
  display: flex; }

.TurnManagementText {
  color: #666666;
  opacity: 0.7;
  font-size: 14px;
  flex: 1;
  height: 22px;
  line-height: 22px;
  padding-left: 20px;
  font-weight: bold; }

.Turn-header-right-content {
  flex: 3;
  display: flex;
  justify-content: flex-end; }

.turn-search-header {
  position: relative; }
  .turn-search-header .turn-search-icon {
    position: absolute;
    width: 18px;
    height: 22px;
    line-height: 22px;
    color: #999999;
    left: 7px; }
  .turn-search-header .TurnManagementSearchBar {
    width: 171px;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: 22px;
    font-family: Roboto;
    font-size: 12px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px; }
  .turn-search-header .searchingtext {
    color: #000000; }

.turn-header-create {
  position: relative; }
  .turn-header-create .turn-create-icon {
    position: absolute;
    width: 12px;
    height: 22px;
    line-height: 26px;
    font-weight: bold;
    color: #fff;
    left: 15px;
    font-size: 12px; }
  .turn-header-create .CreateTurnTemplate {
    width: 151px;
    border-radius: 20px;
    margin-left: 5px;
    color: #ffffff;
    background-color: #e61f1d;
    height: 22px;
    font-size: 12px;
    font-family: "Roboto";
    font-stretch: normal;
    font-style: normal;
    line-height: 22px-3;
    letter-spacing: normal;
    text-align: left;
    padding-left: 28px; }
